let WebIM = require("../../utils/WebIM")["default"];
let disp = require("../../utils/broadcast");

export default {
  data() {
    return {
      username: "",
      switchStatus: ""
    };
  },

  components: {},
  props: {},
  onLoad: function () {
    let myUsername = wx.getStorageSync("myUsername");
    let me = this;
    this.setData({
      username: myUsername
    });
    this.setData({
      switchStatus: WebIM.config.isDebug
    });
  },

  onShow() {
    this.setData({
      switchStatus: WebIM.config.isDebug
    });
  },

  methods: {
    openDebug(event) {
      WebIM.isDebug({
        isDebug: event.detail.value
      });
    }

  }
};
